"use strict";

module.exports = function(app){

	require('./styles.css');
	
	app.controller( 'createCtrl', require('./createCtrl') );

    app.config(function($stateProvider){

        $stateProvider.state('categories.create', {
            url: '/create',
            template: require('./create.html'),
            controller: 'createCtrl as vm'
        });

    });

};


